import Vue from 'vue'
import Router from 'vue-router'
import App from '../App.vue'

const List = resolve => require(['../page/city/List.vue'], resolve)
const Search = resolve => require(['../page/Search/Search.vue'], resolve)
const Goods = resolve => require(['../page/Good/Goods.vue'], resolve)
const Register = resolve => require(['../page/Register/Register.vue'], resolve)
const Du = resolve => require(['../page/Register/Du.vue'], resolve)
const Header = resolve => require(['../components/Header.vue'], resolve)
const My = resolve => require(['../page/cart/My.vue'], resolve)
const Geren = resolve => require(['../page/cart/Geren.vue'], resolve)
const Footers = resolve => require(['../components/footers.vue'], resolve)
const Csvuex = resolve => require(['../cs/csvuex.vue'], resolve)
const Added = resolve => require(['../page/cart/Added.vue'], resolve)
const Add = resolve => require(['../page/cart/Add.vue'], resolve)
const Shopcont = resolve => require(['../page/Good/Shopcont.vue'], resolve)
const Cartcontrol = resolve => require(['../page/Good/Cartcontrol.vue'], resolve)
const Order = resolve => require(['../page/order/order.vue'], resolve)

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			component: App,
			children: [
				{
					path: '/List',
					name: 'List',
					component: List
				},
				{
					path: '/Search:id',
					name: 'Search',
					component: Search
				},
				{
					path: '/',
					name: 'Goods',
					component: Goods
				},
				{
					path: '/Register',
					name: 'Register',
					component: Register
				},
				{
					path: '/Du',
					name: 'Du',
					component: Du
				},
				{
					path: '/Header',
					name: 'Header',
					component: Header
				},
				{
					path: '/My',
					name: 'My',
					component: My
				},
				{
					path: '/GeRen',
					name: 'Geren',
					component: Geren
				},
				{
					path: '/Footers',
					name: 'Footers',
					component: Footers
				},
				{
					path: '/Shopcont',
					name: 'Shopcont',
					component: Shopcont
				},
				{
					path: '/Cartcontrol',
					name: 'Cartcontrol',
					component: Cartcontrol
				},
				{
					path: '/Csvuex',
					name: 'Csvuex',
					component: Csvuex
				},
				{
					path: '/Added',
					name: 'Added',
					component: Added
				},
				{
					path: '/Add',
					name: 'Add',
					component: Add
				},
				{
					path: '/Order',
					name: 'Order',
					component: Order
				}
			]
		},

	]
})



