import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersist from 'vuex-persist'

const loca = new VuexPersist({
	storage: window.localstorage
})

Vue.use(Vuex)
const store = new Vuex.Store({
	state: {
		arr: '',
		login: {
			username: '',
			log: false
		},
		img:''

	},
	//对state数据进行二次处理
	getters: {},
	// 进行异步处理
	actions: {},
	//进行使用插件
	plugins: [
		loca.plugin
	],
	//定义事件
	mutations: {
		faA(state, val) {
			state,arr += val
		},
		loca(state, val) {
			state.login = val
		},
		tp() {
			state.img=''
		}
	}
})
export default store


